}

.order-history-details-order-title {
	margin-bottom:$sc-margin-lv2;
}

.order-history-details-picked-instore-title {
	padding: 0 $sc-padding-lv4 $sc-padding-lv4 0;
}
.order-history-details-picked-instore-items {
	margin-bottom: $sc-margin-lv1;
	border-top:1px solid $sc-color-theme-border-light;
}

