	@extend .order-history-details-payment-transactions;
}

.order-history-details-payment-table {
	@extend .order-history-details-payment-transactions-table;
}

.order-history-details-payment-table-header-payment-transaction {
	@extend .order-history-details-payment-transactions-table-header-payment-transaction !optional;
}

.order-history-details-payment-table-header-date {
	@extend .order-history-details-payment-transactions-table-header-date !optional;
}

.order-history-details-payment-table-body-payment-transaction {
	@extend .order-history-details-payment-transactions-table-body-payment-transaction;
}

.order-history-details-payment-table-body-payment-transaction {
	@extend .order-history-details-payment-transactions-table-body-payment-transaction;
}

.order-history-details-payment-method-title {
	margin-bottom: $sc-margin-lv4;
}

.order-history-details-payment-row {
	@extend .row;
	margin-bottom: $sc-margin-lv4;
}

.order-history-details-header-date-info-date-label,
.order-history-details-header-purchase-order-info-purchase-order-number-label,
.order-history-details-header-amount-info-amount-label,
.order-history-details-header-quote-info-quote-label {
	@extend .sc-label;
}
